function createMarkup(array) {
    return array
        .map(({original,preview, description }) => `
   <li class = "gallery-item">
      <a class="gallery-link" href="${original}">
            <img class = "gallery-image"
            src = "${preview}" 
            data-source = "large-image.jpg"
            alt = "${description}" /> 
      </a>
   </li>
   `)
      .join("");
}


